'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, LineChart, Line, PieChart, Pie, Cell, AreaChart, Area } from 'recharts'

interface AnalyticsData {
  totalStudents: number
  totalInstructors: number
  totalClasses: number
  totalRevenue: number
  monthlyGrowth: number
  attendanceRate: number
}

export default function AnalyticsPage() {
  const [analyticsData, setAnalyticsData] = useState<AnalyticsData | null>(null)
  const [selectedPeriod, setSelectedPeriod] = useState('monthly')
  const [isLoading, setIsLoading] = useState(true)

  // Mock data for charts
  const monthlyRevenueData = [
    { month: 'Jan', revenue: 45000, students: 120, classes: 85 },
    { month: 'Feb', revenue: 52000, students: 135, classes: 92 },
    { month: 'Mar', revenue: 48000, students: 128, classes: 88 },
    { month: 'Apr', revenue: 61000, students: 156, classes: 105 },
    { month: 'May', revenue: 58000, students: 148, classes: 98 },
    { month: 'Jun', revenue: 67000, students: 172, classes: 115 }
  ]

  const subjectDistributionData = [
    { name: 'Mathematics', value: 35, color: '#3B82F6' },
    { name: 'Physics', value: 25, color: '#8B5CF6' },
    { name: 'Chemistry', value: 20, color: '#10B981' },
    { name: 'Biology', value: 15, color: '#F59E0B' },
    { name: 'English', value: 5, color: '#EF4444' }
  ]

  const attendanceData = [
    { day: 'Mon', attendance: 85 },
    { day: 'Tue', attendance: 92 },
    { day: 'Wed', attendance: 78 },
    { day: 'Thu', attendance: 88 },
    { day: 'Fri', attendance: 95 },
    { day: 'Sat', attendance: 82 },
    { day: 'Sun', attendance: 76 }
  ]

  const instructorPerformanceData = [
    { name: 'Dr. Mohamed Ali', students: 45, rating: 4.8, revenue: 15000 },
    { name: 'Prof. Sarah Ahmed', students: 38, rating: 4.6, revenue: 12500 },
    { name: 'Mr. Omar Khaled', students: 52, rating: 4.9, revenue: 18000 },
    { name: 'Dr. Amina Hassan', students: 29, rating: 4.5, revenue: 9500 },
    { name: 'Prof. Youssef Ibrahim', students: 33, rating: 4.7, revenue: 11000 }
  ]

  useEffect(() => {
    // Simulate loading analytics data
    const loadAnalytics = async () => {
      await new Promise(resolve => setTimeout(resolve, 1500))
      setAnalyticsData({
        totalStudents: 156,
        totalInstructors: 12,
        totalClasses: 24,
        totalRevenue: 67000,
        monthlyGrowth: 15.2,
        attendanceRate: 87.5
      })
      setIsLoading(false)
    }
    loadAnalytics()
  }, [])

  const exportData = (format: string) => {
    // Simulate data export
    const data = monthlyRevenueData.map(item => 
      `${item.month},${item.revenue},${item.students},${item.classes}`
    ).join('\n')
    
    const csvContent = `Month,Revenue,Students,Classes\n${data}`
    const blob = new Blob([csvContent], { type: 'text/csv' })
    const url = window.URL.createObjectURL(blob)
    const link = document.createElement('a')
    link.href = url
    link.download = `analytics_${format}_${new Date().toISOString().split('T')[0]}.csv`
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
    window.URL.revokeObjectURL(url)
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-96">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-purple-500 mx-auto mb-4"></div>
          <p className="text-lg text-slate-600">Loading analytics...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="flex flex-col md:flex-row md:items-center md:justify-between space-y-4 md:space-y-0">
        <div>
          <h1 className="text-3xl font-bold bg-gradient-to-r from-slate-900 to-slate-600 bg-clip-text text-transparent">
            Center Analytics
          </h1>
          <p className="text-slate-600 mt-2">
            Comprehensive insights and performance metrics
          </p>
        </div>
        <div className="flex space-x-3">
          <Select value={selectedPeriod} onValueChange={setSelectedPeriod}>
            <SelectTrigger className="w-40">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="weekly">Weekly</SelectItem>
              <SelectItem value="monthly">Monthly</SelectItem>
              <SelectItem value="yearly">Yearly</SelectItem>
            </SelectContent>
          </Select>
          <Button 
            onClick={() => exportData('monthly')}
            variant="outline"
            className="px-6"
          >
            Export Data
          </Button>
        </div>
      </div>

      {/* Key Metrics */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card className="border-0 shadow-lg bg-gradient-to-r from-blue-50 to-cyan-50">
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-slate-600">Total Students</p>
                <p className="text-2xl font-bold text-blue-600">{analyticsData?.totalStudents}</p>
                <p className="text-xs text-green-600 mt-1">+{analyticsData?.monthlyGrowth}% this month</p>
              </div>
              <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center">
                <div className="w-6 h-6 bg-blue-500 rounded-full"></div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-lg bg-gradient-to-r from-purple-50 to-pink-50">
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-slate-600">Total Instructors</p>
                <p className="text-2xl font-bold text-purple-600">{analyticsData?.totalInstructors}</p>
                <p className="text-xs text-green-600 mt-1">+2 new this month</p>
              </div>
              <div className="w-12 h-12 bg-purple-100 rounded-full flex items-center justify-center">
                <div className="w-6 h-6 bg-purple-500 rounded-full"></div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-lg bg-gradient-to-r from-green-50 to-emerald-50">
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-slate-600">Active Classes</p>
                <p className="text-2xl font-bold text-green-600">{analyticsData?.totalClasses}</p>
                <p className="text-xs text-green-600 mt-1">+8 this week</p>
              </div>
              <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center">
                <div className="w-6 h-6 bg-green-500 rounded-full"></div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-lg bg-gradient-to-r from-yellow-50 to-orange-50">
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-slate-600">Total Revenue</p>
                <p className="text-2xl font-bold text-orange-600">EGP {analyticsData?.totalRevenue.toLocaleString()}</p>
                <p className="text-xs text-green-600 mt-1">+12% this month</p>
              </div>
              <div className="w-12 h-12 bg-orange-100 rounded-full flex items-center justify-center">
                <div className="w-6 h-6 bg-orange-500 rounded-full"></div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Charts Row 1 */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Revenue Trend */}
        <Card className="border-0 shadow-lg">
          <CardHeader>
            <CardTitle className="text-lg font-bold text-slate-900">Revenue Trend</CardTitle>
            <CardDescription>Monthly revenue over the past 6 months</CardDescription>
          </CardHeader>
          <CardContent>
            <ResponsiveContainer width="100%" height={300}>
              <AreaChart data={monthlyRevenueData}>
                <CartesianGrid strokeDasharray="3 3" stroke="#f1f5f9" />
                <XAxis dataKey="month" stroke="#64748b" />
                <YAxis stroke="#64748b" />
                <Tooltip 
                  contentStyle={{ 
                    backgroundColor: 'white', 
                    border: '1px solid #e2e8f0',
                    borderRadius: '8px',
                    boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)'
                  }}
                />
                <Area 
                  type="monotone" 
                  dataKey="revenue" 
                  stroke="#3B82F6" 
                  fill="url(#colorRevenue)" 
                  strokeWidth={2}
                />
                <defs>
                  <linearGradient id="colorRevenue" x1="0" y1="0" x2="0" y2="1">
                    <stop offset="5%" stopColor="#3B82F6" stopOpacity={0.3}/>
                    <stop offset="95%" stopColor="#3B82F6" stopOpacity={0}/>
                  </linearGradient>
                </defs>
              </AreaChart>
            </ResponsiveContainer>
          </CardContent>
        </Card>

        {/* Subject Distribution */}
        <Card className="border-0 shadow-lg">
          <CardHeader>
            <CardTitle className="text-lg font-bold text-slate-900">Subject Distribution</CardTitle>
            <CardDescription>Percentage of classes by subject</CardDescription>
          </CardHeader>
          <CardContent>
            <ResponsiveContainer width="100%" height={300}>
              <PieChart>
                <Pie
                  data={subjectDistributionData}
                  cx="50%"
                  cy="50%"
                  innerRadius={60}
                  outerRadius={100}
                  paddingAngle={5}
                  dataKey="value"
                >
                  {subjectDistributionData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip 
                  contentStyle={{ 
                    backgroundColor: 'white', 
                    border: '1px solid #e2e8f0',
                    borderRadius: '8px',
                    boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)'
                  }}
                />
              </PieChart>
            </ResponsiveContainer>
            <div className="flex flex-wrap justify-center gap-4 mt-4">
              {subjectDistributionData.map((item, index) => (
                <div key={index} className="flex items-center space-x-2">
                  <div 
                    className="w-3 h-3 rounded-full" 
                    style={{ backgroundColor: item.color }}
                  ></div>
                  <span className="text-sm text-slate-600">{item.name}</span>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Charts Row 2 */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Weekly Attendance */}
        <Card className="border-0 shadow-lg">
          <CardHeader>
            <CardTitle className="text-lg font-bold text-slate-900">Weekly Attendance Rate</CardTitle>
            <CardDescription>Average attendance percentage by day</CardDescription>
          </CardHeader>
          <CardContent>
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={attendanceData}>
                <CartesianGrid strokeDasharray="3 3" stroke="#f1f5f9" />
                <XAxis dataKey="day" stroke="#64748b" />
                <YAxis stroke="#64748b" />
                <Tooltip 
                  contentStyle={{ 
                    backgroundColor: 'white', 
                    border: '1px solid #e2e8f0',
                    borderRadius: '8px',
                    boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)'
                  }}
                />
                <Bar 
                  dataKey="attendance" 
                  fill="#10B981" 
                  radius={[4, 4, 0, 0]}
                />
              </BarChart>
            </ResponsiveContainer>
          </CardContent>
        </Card>

        {/* Instructor Performance */}
        <Card className="border-0 shadow-lg">
          <CardHeader>
            <CardTitle className="text-lg font-bold text-slate-900">Top Instructors</CardTitle>
            <CardDescription>Performance by student count and revenue</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {instructorPerformanceData.map((instructor, index) => (
                <div key={index} className="flex items-center justify-between p-3 bg-slate-50 rounded-lg">
                  <div className="flex-1">
                    <div className="font-medium text-slate-900">{instructor.name}</div>
                    <div className="text-sm text-slate-600">
                      {instructor.students} students • Rating: {instructor.rating}/5
                    </div>
                  </div>
                  <div className="text-right">
                    <div className="font-semibold text-green-600">
                      EGP {instructor.revenue.toLocaleString()}
                    </div>
                    <div className="text-xs text-slate-500">Revenue</div>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Export Section */}
      <Card className="border-0 shadow-lg">
        <CardHeader>
          <CardTitle className="text-lg font-bold text-slate-900">Data Export</CardTitle>
          <CardDescription>Export analytics data in various formats</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Button 
              onClick={() => exportData('daily')}
              variant="outline" 
              className="p-6 h-auto"
            >
              <div className="text-center">
                <div className="font-semibold">Daily Report</div>
                <div className="text-sm text-slate-600">Export today's data</div>
              </div>
            </Button>
            <Button 
              onClick={() => exportData('weekly')}
              variant="outline" 
              className="p-6 h-auto"
            >
              <div className="text-center">
                <div className="font-semibold">Weekly Report</div>
                <div className="text-sm text-slate-600">Export this week's data</div>
              </div>
            </Button>
            <Button 
              onClick={() => exportData('monthly')}
              variant="outline" 
              className="p-6 h-auto"
            >
              <div className="text-center">
                <div className="font-semibold">Monthly Report</div>
                <div className="text-sm text-slate-600">Export this month's data</div>
              </div>
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
